import React,{useState} from 'react'
import SearchBox from './SearchBox';
import ResultBox from './ResultBox';
import { isEqual } from 'lodash';


const init ={
    searchkey:"",
    startDate:"",
    endDate:""
};
function Body(){
    const [inputdata,setinputdata]=useState(init);
    
    const getData=(data)=>{
        if(isEqual(inputdata,data)===false)
            setinputdata({...data});
    }

    return (
        <div className='App'>
            <SearchBox sendData={getData}/>
            <ResultBox inputdata={{...inputdata}}/>
        </div>
    )
}

export default Body
