import React,{useEffect,useReducer,useState} from 'react'

let init={
    searchkey:"",
    startDate:"",
    endDate:""
};

const reducer=(state,action)=>{
    switch(action.type){
        case "searchkey":
            return {...state,searchkey: action.data}
        case "startDate":
            return {...state,startDate: action.data};
        case "endDate":
            return {...state,endDate: action.data};
        case "reset":
            return action.data;
        default:
            return state;
    }
}


function SearchBox({sendData}) {
    const [keyerror,setkeyerror]=useState(false);
    const [daterror,setdaterror]=useState(false);
    const [inputdata,dispatch]=useReducer(reducer,init);
    

    useEffect(()=>{
        setkeyerror(false);
    },[inputdata.searchkey]);

    useEffect(()=>{
        setdaterror(false);
    },[inputdata.startDate,inputdata.endDate]);

    let handlesubmit=(e)=>{
        e.preventDefault();
        if(inputdata.searchkey!==""&&inputdata.endDate>=inputdata.startDate){
            sendData({...inputdata,page:0});
            dispatch({type:"reset",data:init})
        }
        else{
            if(inputdata.searchkey==="")
                setkeyerror(true);
            if(inputdata.endDate<inputdata.startDate)
                setdaterror(true);
        }
    }

    let handlechange=(e)=>{
        e.preventDefault();
        dispatch({
            type: e.target.id,
            data: e.target.value
        })
    }

    return (
        <div>
            <h1>Search Articles</h1>
            <div>
                <form onSubmit={handlesubmit} className="form-main">
                    <div className="inputbox">
                        <div className="mb-3">
                            <label htmlFor="searchkey" className="form-label">Search Topic<span className='red'>*</span></label>
                            <input type="text" value={inputdata.searchkey} onChange={handlechange} className="form-control" id="searchkey" aria-describedby="emailHelp" placeholder='Enter Your Topic of Interest'/>
                            <span className={keyerror?"red":"hide"}> Please fill the required field </span>
                        </div>
                        <div>
                        <div className="mb-3">
                            <label htmlFor="startDate" className="form-label">Start Date</label>
                            <input type="date" value={inputdata.startDate} onChange={handlechange} className="form-control" id="startDate"/>
                            
                        </div>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="endDate">End Date</label>
                            <input type="date" value={inputdata.endDate} onChange={handlechange} className="form-control" id="endDate"/>
                            <span className={daterror?"red":"hide"}> Enter correct start and end dates</span>
                        </div>
                        </div>
                    </div>
                    <button type="submit" className="btn-main btn btn-primary">Get Articles</button>
                </form>
            </div>
        </div>
    )
}

export default React.memo(SearchBox)